<template>
    <div class="container">
        <div class="row">
            <div class="col s12 m8 offset-m2">
                <div class="login card-panel grey lighten-4 black-text center">
                    <h3>Register</h3>
                    <form>
                    <div class="input-field">
                        <i class="material-icons prefix">email</i>
                        <input type="text" id="email" v-model="email">
                        <label for="email" class="black-text">Email</label>
                    </div>
                    <div class="input-field">
                        <i class="material-icons prefix">lock</i>
                        <input type="password" id="password" v-model="password">
                        <label for="password" class="black-text">Password</label>
                    </div>
                    <button v-on:click="register" class="btn btn-large grey lighten-4 black-text">Register</button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
/* eslint-disable */
import firebase from 'firebase'
export default {
    name: "register",
    data: function() {
        return{
            email: '',
            password: ''
        }
    },
    methods: {
        register(e) {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(user=>{
                alert(`Account Created for ${user.email}`)
                this.$router.push('/')
            },
            err => {
                alert(err.message)
            })
             e.preventDefault()
        }
    }

}
</script>


<style scoped>
   .input-field .prefix.active {
        color:#000;
    }
     .input-field .prefix {
        color:#000;
        
    }
    input[type=text]:not(.browser-default) ,  input[type=text]:not(.browser-default):focus {
        border-bottom: 1px solid #000;
        box-shadow:0 1px 0 0#000;
    }
    input[type=password]:not(.browser-default), input[type=password]:not(.browser-default):focus  {
        border-bottom: 1px solid #000;
        box-shadow:0 1px 0 0#000;
    }
    
</style>




